<template>
  <div class=" advantage">
    <el-row class="container">
      <el-col :span="8">
        <img src="static/images/index/safe.png" style="height: 130px;" class="slide-top"/>
        <h3>{{ $t("index.part1") }}</h3>
        <p>{{ $t("index.part2") }}</p>
      </el-col>
      <el-col :span="8">
        <img src="static/images/index/modal.png" alt="" style="height: 130px;" class="slide-top"/>
        <h3>{{ $t("index.part4") }}</h3>
        <p>{{ $t("index.part5") }}</p>
      </el-col>
      <el-col :span="8">
        <img src="static/images/index/service.png" style="height: 130px;" class="slide-top"/>
        <h3>{{ $t("index.part7") }}</h3>
        <p>{{ $t("index.part8") }}</p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import scrollReveal from 'scrollreveal'
  export default {
    data() {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    },
    mounted() {
      const sr = scrollReveal()

      sr.reveal('.fade-in', { duration: 500, reset: true })
      sr.reveal('.scale-in', { scale: 0.5, duration: 500, reset: true })
      sr.reveal('.slide-left', { origin: 'left', distance: '200px', duration: 500, reset: true })
      sr.reveal('.slide-top', { origin: 'top', distance: '200px', duration: 1000, reset: false })
    }
  }
</script>

<style scoped lang="less">
  .advantage{
    text-align: center;
    padding: 90px 0;
    background: white;
  }
  
  .advantage h3{
    margin: 25px 0;
    color: rgb(62, 69, 88);
    font-weight: normal;
    font-size: 20px;
  }
  .advantage p{
    color: #8a8d97;
    margin-bottom: 0;
    font-size: 14px;
    width: 290px;
    margin: 0 auto;
    line-height: 21px;
  }
</style> 
